@use 'abstracts' as *;

.split {
   @include m(parent) {
      // outline: 1px solid white;
      @include flexy();
      gap: var(--gap);
      @include mq(max-width, large) {
         @include flexy($dir: column, $align: center);
      }

      .header--pm & {
         @include mq($width: max-width, $key: large) {
            --gap: 1.8rem;
         }
      }

      .header--home & {
         @include mq(min-width, large) {
            --gap: 8rem;
         }
      }

      .container--build & {
         @include mq($width: max-width, $key: tablet) {
            --gap: 12rem;
         }
      }

      .container--testimonials & {
         --gap: 4rem;
         @include mq(min-width, large) {
            --gap: 2rem;
         }
      }

      // children
      & > * {
         position: relative;
         @include mq(min-width, large) {
            flex: 1 100%;
         }
      }

      @include m(offset) {
         @include mq(min-width, large) {
            & > * {
               flex: 0 1 auto;
            }
         }
      }

      @include m(about) {
         @include mq(min-width, large) {
            --gap: 10.5rem;
            min-height: 6rem;
         }
      }

      @include m(contact) {
         @include mq(min-width, large) {
            --gap: 14rem;
         }
      }
   }
}
